import { css } from 'lit';

export const styles = css`:host{--component-size: var(--ig-size, var(--default-size))}[part=base]{color:var(--color);background:var(--background);width:var(--size);height:var(--size)}[part=base] [part=initials]{font-size:calc(var(--size)/2);line-height:calc(var(--size)/2)}:host([shape=rounded]) [part=base]{border-radius:var(--border-radius)}:host([shape=circle]) [part=base]{border-radius:calc(var(--size)/2)}`;
